.two-column-slider-text {
	padding: 4rem 0;
}

.two-column-slider-text__inner {
	align-items: stretch;
	display: grid;
	grid-template-columns: 1fr;
	margin: 0 auto;
	max-width: 1200px;
	min-height: 500px;
	padding: 0 1rem;
}

.two-column-slider__repeater {
	height: 100%;
	position: relative;
	width: 100%;
}

/* Splide slider container */
.benefits-card-container {
	height: 100%;
	position: relative;
}

/* Splide track and list full height */
.benefits-card-container .splide__track {
	height: 100%;
}

.benefits-card-container .splide__list {
	height: 100%;
}

.benefits-card-container .splide__slide {
	height: 100%;
}

/* Splide pagination styling */
.benefits-card-container .splide__pagination {
	align-items: center;
	bottom: 25px;
	display: flex;
	gap: 0.75rem;
	justify-content: center;
	left: 50%;
	list-style: none;
	margin: 0;
	padding: 0;
	position: absolute;
	transform: translateX(-50%);
}

.benefits-card-container .splide__pagination li {
	margin: 0;
	padding: 0;
}

.benefits-card-container .splide__pagination__page {
	background: var(--wp--preset--color--accent);
	border: none;
	border-radius: 50%;
	cursor: pointer;
	height: 12px;
	opacity: 0.7;
	outline: none;
	transition: all 0.3s ease;
	width: 12px;
}

.benefits-card-container .splide__pagination__page:hover {
	background: #94a3b8;
	opacity: 1;
	transform: scale(1.1);
}

.benefits-card-container .splide__pagination__page.is-active {
	background: #2f17b9;
	opacity: 1;
	transform: scale(1.2);
}

/* Slide items */
.two-column-slider-text__item {
	align-items: center;
	display: flex;
	height: 100%;
	justify-content: center;
	padding: 0;
}

.two-column-slider-text__item-inner {
	aspect-ratio: none;
	background: white;
	border-radius: 0;
	height: 100%;
	max-width: none;
	min-height: 500px;
	overflow: hidden;
	position: relative;
	/* transition: transform 0.3s ease; */
	width: 100%;
}

/* .two-column-slider-text__item-inner:hover {
	transform: translateY(-4px);
} */

.two-column-slider-text__location-image {
	height: 100%;
	width: 100%;
}

.two-column-slider-text__location-image img {
	height: 100%;
	object-fit: cover;
	width: 100%;
}

.two-column-slider-text__partner-logo {
	align-items: center;
	background: white;
	border: 2px solid white;
	border-radius: 50%;
	box-shadow: 0 8px 25px -5px rgb(0 0 0 / 0.1);
	display: flex;
	height: 145px;
	justify-content: center;
	left: 1.5rem;
	padding: 0.75rem;
	position: absolute;
	top: 1.5rem;
	width: 145px;
}

.two-column-slider-text__partner-logo img {
	height: 100%;
	object-fit: contain;
	width: 100%;
}

/* Content section */
.two-column-slider-text__content {
	background-color: var(--wp--preset--color--white);
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: center;
	padding: 2rem;
}

.two-column-slider-text__description {
	color: #1f2937;
	font-size: 1.25rem;
	line-height: 1.8;
}

.two-column-slider-text__description h2,
.two-column-slider-text__description h3 {
	color: #111827;
	font-size: 2.5rem;
	font-weight: 700;
	line-height: 1.2;
	margin-bottom: 2rem;
}

.two-column-slider-text__description ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.two-column-slider-text__description li {
	color: var(--wp--preset--color--neutral--black);
	font-size: 1.375rem;
	font-weight: 500;
	line-height: 1.6;
	margin-bottom: 1.5rem;
	padding-left: 2rem;
	position: relative;
}

.two-column-slider-text__description li::before {
	color: var(--wp--preset--color--neutral--black);
	content: "â€¢";
	font-size: 1.5rem;
	font-weight: bold;
	left: 0;
	position: absolute;
	top: 0;
}

.two-column-slider-text__description li:last-child {
	margin-bottom: 0;
}

.two-column-slider-text__description p {
	font-size: 1.125rem;
	line-height: 1.7;
	margin-bottom: 1.5rem;
}

.two-column-slider-text__description p:last-child {
	margin-bottom: 0;
}

/* Mobile optimizations */
@media (max-width: 767px) {
	.two-column-slider-text {
		padding: 2rem 0;
	}

	.two-column-slider-text__inner {
		min-height: 400px;
	}

	.two-column-slider-text__item-inner {
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		min-height: 400px;
	}

	.two-column-slider-text__content {
		border-bottom-left-radius: 20px;
		border-bottom-right-radius: 20px;
		padding: 2rem 1.5rem;
	}

	.two-column-slider-text__description h2,
	.two-column-slider-text__description h3 {
		font-size: 2rem;
		margin-bottom: 1.5rem;
	}

	.two-column-slider-text__description li {
		font-size: 1.125rem;
		margin-bottom: 1rem;
	}

	.benefits-card-container .splide__pagination {
		bottom: 15px;
		gap: 0.5rem;
	}

	.benefits-card-container .splide__pagination__page {
		height: 10px;
		width: 10px;
	}
}

/* Desktop specific styles */
@media (min-width: 768px) {
	.two-column-slider-text__inner {
		grid-template-columns: 1fr 1fr;
		min-height: 600px;
	}

	.two-column-slider-text__content {
		border-bottom-right-radius: 20px;
		border-top-right-radius: 20px;
	}

	.two-column-slider-text__item-inner {
		border-bottom-left-radius: 20px;
		border-top-left-radius: 20px;
		min-height: 600px;
	}
}
